*
{
    margin: 0;
    padding: 0;
}

article
{
    width: 60%;
    margin: auto;
}

.cart_img
{
    display: flex;
    width: 400px;
}

.cart_img img
{
    width: 50px;
    height: 50px;
}

.cart_img p
{
    font-weight: bold;
    margin-left: 20px;
}

.cart_box
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    border-bottom: 3px solid skyblue;
    padding-bottom: 20px;
}

.plus_minus button
{
    padding: 5px 10px;
    font-weight: bold;
    margin-right: 5px;
}

.cart_box div:nth-child(3) button
{
    padding: 5px 10px;
    font-weight: bold;
    letter-spacing: 2px;
    border: none;
    outline: none;
    color: white;
    background-color: rgb(76, 109, 201);
    border-radius: 5px;
    cursor: pointer;
}

.cart_box div:nth-child(3) button:hover
{
    background-color: rgb(248, 37, 37);
}

.cart_box div:nth-child(3) span
{
    background-color: lightgreen;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 10px;
}

.total
{
    display: flex;
    justify-content: space-between;
}

.total span:nth-child(1)
{
    font-size: 2rem;
    color: skyblue;
    font-weight: bold;
    letter-spacing: 1px;
}

.total span:nth-child(2)
{
    font-size: 2rem;
    color: green;
    font-weight: bold;
}